import { useDate } from '@/hooks/useDate'
import { useIndex } from '@/hooks/useIndex'

export type Title = string
export type Subtitle = string
export type Note = string
export type Tags = string[]
export type Step = {
    text: string
    done: boolean
}
export type Steps = Step[]
export type Index = number
export type Date = string
export type Type = 'task' | 'goal'
export type DefaultCollection = 'processing' | 'pinned' | 'done'
export type FromCollection = string
export type ProgressStatus = 'processing' | 'pinned' | 'done'
/**
 * GoalGoals
 * useGoal()Goal,useGoals()
 */
export type Goal = {
    /**
     * indexGoalUNIX
     */
    index: number

    /**
     * Goal
     */
    title: string

    /**
     * Goal
     */
    description?: string
}
/**
 * GoalsGoal
 */
export type Schedule = 'daily' | 'weekly' | 'monthly'
/**
 * GoalsGoal
 */
export interface Goals {
    /**
     * Goal
     */
    goals: Goal[]

    /**
     * Goal
     */
    schedule: Schedule

    /**
     * goals
     * goals
     */
    currentIndex: number

    /**
     * currentIndex
     */
    maxIndex: number

    /**
     * Goals
     */
    createdDate: Date

    /**
     * []
     */
    targetDate: Date | null

    /**
     * Goals
     * currentIndexmaxIndexcompletetrue
     */
    compelete: boolean

    /**
     * GoalsUNIX
     */
    index: number
}

/**
 * TaskTodoGoal
 * useTask(...)TaskuseTask
 */
export interface Task {
    // Task
    title: Title

    /**
     * 
     */
    subtitle: Subtitle

    /**
     * noteTask
     */
    note: Note

    /**
     * 
     * tagsTasktags
     */
    tags: Tags

    /**
     * stepstextdone
     */
    steps: Steps

    /**
     * Task
     * indexTaskkey
     * TaskUNIX
     * storepush
     */
    index: Index

    /**
     * Task
     * 
     * storepush
     */
    createdDate: Date

    /**
     * Task
     * null
     * 
     * storepush
     */
    targetDate: Date | null

    /**
     * Task
     *  task
     *  goal
     *  collection
     */
    type: Type

    /**
     * typecollectionfromCollectionnull
     */
    fromCollection:  DefaultCollection | FromCollection

    /**
     * typegoalgoalStepsnull
     */
    goalSteps: Goals | null

    /**
     * 
     * processingdonepinnedprocessing
     */
    progressStatus: ProgressStatus
}
export type Tasks = Task[]

/**
 * moment().format('x')index
 */
const createIndex = (): number => useIndex()

/**
 * YYYY-MM-DD
 * @returns YYYY-MM-DD
 */
const createDate = (): string => useDate()

/**
 * Task
 * title,
 */
export function useTask({
    title,
    subtitle = '',
    note = '',
    tags = [],
    steps = [],
    targetDate = null,
    fromCollection = 'processing',
}: {
    title: string
    subtitle?: string
    note?: string
    tags?: Tags
    steps?: Steps
    targetDate?: Date | null
    fromCollection: DefaultCollection | FromCollection
}): Task {
    return {
        title: title,
        subtitle: subtitle,
        note: note,
        tags: tags,
        steps: steps,
        index: createIndex(),
        createdDate: createDate(),
        targetDate: targetDate,
        goalSteps: [],
        type: 'task',
        progressStatus: 'processing',
        fromCollection: fromCollection
    }
}

export function useStep({ text, done = false }: { text: string; done?: boolean }): Step {
    return {
        text: text,
        done: done
    }
}

/**
 * Goal
 * GoalGoals
 */
export function useGoal({ title = '', description = '' }: { title?: string; description?: string }): Goal {
    return {
        index: useIndex(),
        title: title,
        description: description
    }
}

/**
 * Goal
 * useGoal()Goal
 */
export function useGoals({
    goalList,
    schedule = 'daily',
    currentIndex = 0,
    maxIndex = goalList.length - 1,
    targetDate = null,
    compelete = false
}: {
    goalList: Goal[]
    schedule?: Schedule
    currentIndex?: number
    maxIndex?: number
    targetDate?: Date | null
    compelete?: boolean
}): Task {
    return {
        title: '',
        subtitle: '',
        note: '',
        tags: [],
        steps: [],
        index: createIndex(),
        createdDate: createDate(),
        targetDate: targetDate,
        fromCollection: 'undefined in goal',
        type: 'goal',
        progressStatus: 'processing',
        goalSteps: {
            goals: goalList,
            schedule: schedule,
            currentIndex: currentIndex,
            maxIndex: maxIndex,
            createdDate: useDate(),
            targetDate: targetDate,
            compelete: compelete,
            index: useIndex()
        }
    }
}